// src/pages/CafeForm.js

import { useEffect, useRef, useState } from "react";
import { Button, FloatingLabel, Form } from "react-bootstrap";
import { initEditor } from "../editor/SmartEditor";
import axios from "axios";
import { useNavigate } from "react-router-dom";

function CafeForm() {

    //SmartEditor    textarea  value      editorTool  
    const [editorTool, setEditorTool] = useState([])

    useEffect(()=>{
        //initEditor()   SmartEditor   textarea  id  
		//textarea  SmartEditor    tool  .  
		setEditorTool(initEditor("content")); // initEditor()   SmartEditor  .
    }, [])

    //    useRef()  
    const inputTitle=useRef()
    const inputContent=useRef()

    const navigate=useNavigate()

    return (
        <>
            <h1>  </h1>
            <Form>
                <FloatingLabel label="" className="mb-3" controlId="title">
                    <Form.Control ref={inputTitle} type="text" placeholder=" ..."/>
                </FloatingLabel>
                <Form.Group className="mb-3"  controlId="content">
                    <Form.Label></Form.Label>
                    <Form.Control ref={inputContent} as="textarea" rows="10"/>
                </Form.Group>
                <Button type="submit" onClick={(e)=>{
                    //  ( )
                    e.preventDefault()
                    // tool   SmartEditor    textarea  value  
                    editorTool.exec();
                    //   
                    const title=inputTitle.current.value
                    const content=inputContent.current.value
                    //axios   api  
                    axios.post("/cafes", {title, content})
                    .then(res=>{
                        console.log(res.data)
                        navigate(`/cafes`)
                    })
                    .catch(error=>{
                        console.log(error)
                    })
                }}></Button>
            </Form>
        </>
    );
}

export default CafeForm;