// src/pages/GalleryForm.js

import axios from "axios";
import { useRef, useState } from "react";
import { Button, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";


function GalleryForm() {

    const dropZone = useRef()
    const imageInput = useRef()
    const captionInput = useRef()
    //     (UI    )
    const [imageData , setImageData] = useState([])
    const navigate = useNavigate()

    const dropZoneStyle={
        minHeight:"300px",
        border:"3px solid #cecece",
        borderRadius:"10px",
        display:"flex",
        justifyContent:"center",
        alignItems:"center",
        cursor:"pointer",
        flexWrap:"wrap",
        rowGap:"10px",
        columnGap:"10px"
    }

    const selectedStyle={
        width:"200px",
        borderRadius:"10px"
    }

    // drop    
    const handleDrop = (e)=>{
        e.preventDefault()
       
        //drop      
		const files=e.dataTransfer.files;
			
        //    
        const imageFiles=[];
			
        //javascript 
        for(let i=0; i<files.length; i++){
            //i  File 
            const tmp=files[i];
            //         
            const reg=/image/;
            if(!reg.test(tmp.type)){ // type     
                console.log("  ")
                continue; //    
            }
           
            //    
            imageFiles.push(tmp);
            //     
            const reader=new FileReader();
            // DataURL   
            reader.readAsDataURL(tmp);
            // (  )    
            reader.onload=(event)=>{
                //    
                const data=event.target.result;
                //       
                const result=imageFiles.map(item=>{
                    //        
                    if(item.name === tmp.name){
                        item.dataUrl = data // dataUrl      
                    }
                    return item
                })
                // state  
                setImageData(result)
            };
        }
        
        //DataTransfer  
        const dataTransfer = new DataTransfer();
        // imageFiles       DataTransfer     
        imageFiles.forEach(item => dataTransfer.items.add(item));
        
        // input type="file"  DataTransfer      
        imageInput.current.files=dataTransfer.files;
    }
    const handleChange=(e)=>{
        //  (input type="file")      
		const files=e.target.files;
			
        //    
        const imageFiles=[];
			
        //javascript 
        for(let i=0; i<files.length; i++){
            //i  File 
            const tmp=files[i];
            //         
            const reg=/image/;
            if(!reg.test(tmp.type)){ // type     
                console.log("  ")
                continue; //    
            }
           
            //    
            imageFiles.push(tmp);
            //     
            const reader=new FileReader();
            // DataURL   
            reader.readAsDataURL(tmp);
            // (  )    
            reader.onload=(event)=>{
                //    
                const data=event.target.result;
                //       
                const result=imageFiles.map(item=>{
                    //        
                    if(item.name === tmp.name){
                        item.dataUrl = data // dataUrl      
                    }
                    return item
                })
                // state  
                setImageData(result)
            };
        }
    }
    //     
    const handleUpload = ()=>{
        //    FormData   
        const formData=new FormData()
        // caption     
        formData.append("caption", captionInput.current.value)
        //     images   
        const files=imageInput.current.files //   
        for(let i=0; i<files.length; i++){
            formData.append("images", files[i])
        }
       
        axios.post("/gallery", formData, {
            headers:{"Content-Type":"multipart/form-data"}
        })
        .then(res=>{
            console.log(res.data)
            //   
            alert(" !")
            navigate("/gallery")
        })
    }

    return (
        <>
            <Form.Group className="mb-3" controlId="caption">
                <Form.Label></Form.Label>
                <Form.Control ref={captionInput} type="text" name="caption" placeholder=" ..." />
            </Form.Group>
            <Form.Group controlId="image">
                <Form.Label></Form.Label>
                <Form.Control style={{display:"none"}} ref={imageInput} onChange={handleChange} type="file" name="image" accept="image/*" multiple/>
            </Form.Group>
            <a href="about:blank" onClick={(e)=>{
                e.preventDefault()//    
                imageInput.current.click() //  
            }}>
                <div ref={dropZone} onDragOver={(e)=>e.preventDefault()}
                    onDrop={handleDrop} style={dropZoneStyle} className="mb-3">
                    {
                        //item  File    dataUrl      
                        imageData.length > 0 ? imageData.map(item=><img style={selectedStyle} key={item.name} src={item.dataUrl} alt={item.name}/>)
                        : <strong>Drag and Drop Files <br/>or Click Here!</strong>
                    }
                </div>
            </a>
            <Button onClick={handleUpload} variant="success"></Button>
        </>
    );
}

export default GalleryForm;