// src/pages/MemberForm.js

import axios from "axios";
import { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";


function MemberForm() {
    //   javascript   
    const navigate = useNavigate()

    //      {name:"xxx", addr:"xxx"}
    const [state, setState] = useState({})

    //     
    const handleChange = (e)=>{
        //change event      state  .
        setState({
            ...state,
            [e.target.name]:e.target.value
        })
    }
    //    
    const handleSave = ()=>{
        //state   object   ( body  json  )
        axios.post("/members", state)
        .then(res=>{
            console.log(res.data)
            alert(res.data.name+"    ");
            //   "/members/new"   "/members"     .
            navigate("/members")
        })
        .catch(error=>console.log(error))
    }

    return (
        <>
            <h1>  </h1>
            <Form>
                <Form.Group as={Row} className="mb-3" controlId="name">
                    <Form.Label column sm={2}></Form.Label>
                    <Col sm={10}>
                        <Form.Control onChange={handleChange}  type="text" name="name" placeholder=" ..." />
                    </Col>
                </Form.Group>
                <Form.Group as={Row} className="mb-3" controlId="addr">
                    <Form.Label column sm={2}></Form.Label>
                    <Col sm={10}>
                        <Form.Control onChange={handleChange} type="text" name="addr" placeholder=" ..." />
                    </Col>
                </Form.Group>
                <Form.Group as={Row} className="mb-3">
                    <Col sm={{ span: 10, offset: 2 }}>
                        <Button onClick={handleSave} variant="primary" ></Button>
                    </Col>
                </Form.Group>
            </Form>
        </>
    );
}

export default MemberForm;