// src/pages/CafeUpdateForm.js

import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { Button, FloatingLabel, Form } from "react-bootstrap";
import { Link, useNavigate, useParams } from "react-router-dom";
import { initEditor } from "../editor/SmartEditor";

function CafeUpdateForm() {
    // "/cafes/:num/edit"  num      
    const {num} = useParams()

    //SmartEditor    textarea  value      editorTool  
    const [editorTool, setEditorTool] = useState([])
    //    useRef()  
    const inputTitle=useRef()
    const inputContent=useRef()
    //    
    const navigate = useNavigate()
    //       
    const [savedData, setSavedData]=useState({})

    //     
    const handleCancel = ()=>{
        //     
        inputTitle.current.value=savedData.title
        //  SmartEditor    
        editorTool.setContents(savedData.content)
    }

    useEffect(()=>{
        // initEditor()   SmartEditor  .
        setEditorTool(initEditor("content")); 
        //num           .
        axios.get(`/cafes/${num}/edit`)
        .then(res=>{
            //   object  
            console.log(res.data)
            //  state  
            setSavedData(res.data)
            //   UI   
            inputTitle.current.value=res.data.title
            inputContent.current.value=res.data.content
        })
        .catch(error=>{
            console.log(error)
        })
        
        const handleResize = ()=>{
            //    
            setEditorTool(initEditor("content")); 
        }
        //window  resize      
        window.addEventListener("resize", handleResize)
        
        console.log("CafeUpdateForm  ")
        /*
            useEffect()        
               .       
                  . 
        */
        return ()=>{
            console.log(" ?")
            //    
            window.removeEventListener("resize", handleResize)
        }
    }, [])

    return (
        <>
            <nav>
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to="/">Home</Link></li>
                    <li className="breadcrumb-item"><Link to="/cafes">Cafe</Link></li>
                    <li className="breadcrumb-item active">Update</li>
                </ol>
		    </nav>
            <h1>   </h1>
            <Form>
                <FloatingLabel label="" className="mb-3" controlId="title">
                    <Form.Control ref={inputTitle} type="text" placeholder=" ..."/>
                </FloatingLabel>
                <Form.Group className="mb-3"  controlId="content">
                    <Form.Label></Form.Label>
                    <Form.Control ref={inputContent} as="textarea" style={{height:"300px"}}/>
                </Form.Group>
                <Button type="submit" onClick={(e)=>{
                    //  ( )
                    e.preventDefault()
                    // tool   SmartEditor    textarea  value  
                    editorTool.exec();
                    //   
                    const title=inputTitle.current.value
                    const content=inputContent.current.value
                    //axios   api  
                    axios.put(`/cafes/${num}`, {title, content})
                    .then(res=>{
                        console.log(res.data)
                        alert("")
                        //   
                        navigate(`/cafes/${num}`)
                    })
                    .catch(error=>{
                        console.log(error)
                    })
                }}></Button>
                <Button onClick={handleCancel} variant="danger"></Button>
            </Form>
        </>
    );
}

export default CafeUpdateForm;