// src/pages/UserForm.js

import axios from "axios";
import { useState } from "react";
import { Button, Form } from "react-bootstrap";
import AlertModal from "../components/AlertModal"
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";

//userName   
const reg_userName=/^[a-zA-Z].{4,9}$/
//password   
const reg_password=/[\W]/
//email   
const reg_email=/@/

function UserForm() {
    //    
    const [formData, setFormData] = useState({
        userName:"",
        password:"",
        password2:"",
        email:""
    })
    //      
    const [isValid, setValid] = useState({
        userName:false, // userName      isValid.userName  
        password:false, // password      isValid.password  
        email:false // email    isValid.email   
    })
    // userName, password, email    (  )   
    const [isDirty, setDirty] = useState({
        userName:false,
        password:false,
        email:false
    })


    // , ,     
    const handleChange = (e)=>{
        // e.target  object . object    
        const {name, value} = e.target
        
        //      
        if(!isDirty[name]){
            //    
            setDirty({
                ...isDirty,
                [name]:true
            })
        }

        setFormData({
            ...formData,
            [name]:value
        })

        //  
        validate(name, value)
    }

    //validation    
    const validate = (name, value)=>{
        if(name === "userName"){
            //  userName       . 
            axios.get(`/user/check_username/${value}`)
            .then(res=>{
                // res.data   {canUse:true} or {canUse:false}   API   .
                setValid({
                    ...isValid,
                    [name]:reg_userName.test(value) && res.data.canUse
                })
            })
            .catch(error=>{
                console.log(error)
            })
        }else if(name === "password"){
            //   password2     
            const isEqual = value === formData.password2

            setValid({
                ...isValid,
                password:reg_password.test(value) && isEqual
            })

        }else if(name === "password2"){
            //   password     
            const isEqual = value === formData.password

            setValid({
                ...isValid,
                password:reg_password.test(value) && isEqual
            })

        }else if(name === "email"){
            setValid({
                ...isValid,
                [name]:reg_email.test(value)
            })
        }
    }

    //       
    const handleSubmit = (e)=>{
        e.preventDefault()
        //axios      .
        axios.post("/user", formData)
        .then(res=>{
            console.log(res.data)
            setAlertShow(true)
        })
        .catch(error=>{
            console.log(error)
        })
    }
    //    
    const [alertShow, setAlertShow] = useState(false)

    const navigate = useNavigate()
    const dispatch = useDispatch()

    const handleYes = ()=>{
        //   "/"   .
        navigate("/")
        //  .
        const action = {
            type:"LOGIN_MODAL",
            payload:{show:true, message:"   "}
        }
        dispatch(action)
    }
    return (
        <>
            <AlertModal show={alertShow} message={`${formData.userName}   `} 
                yes={handleYes}/>
            <h1>  </h1>
            <Form onSubmit={handleSubmit} noValidate>
                <Form.Group controlId="id" className="mb-3" >
                    <Form.Label></Form.Label>
                    {/* isInvalid      , isValid   */}
                    <Form.Control isValid={isValid.userName} isInvalid={!isValid.userName && isDirty.userName} onChange={handleChange}  type="text" name="userName" placeholder=" ..."/>
                    <div className="form-text">
                          5~10   
                    </div>
                    <Form.Control.Feedback type="invalid">
                           
                    </Form.Control.Feedback>
                </Form.Group>
                <Form.Group controlId="password" className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control isValid={isValid.password} isInvalid={!isValid.password && isDirty.password} onChange={handleChange} type="password" name="password" placeholder=" ..."/>
                    <div className="form-text">
                         1  
                    </div>
                    <Form.Control.Feedback type="invalid">
                         !
                    </Form.Control.Feedback>
                </Form.Group>

                <Form.Group controlId="password2" className="mb-3">
                    <Form.Label> </Form.Label>
                    <Form.Control onChange={handleChange} type="password" name="password2" placeholder=" ..."/>
                </Form.Group>

                <Form.Group controlId="email" className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control isValid={isValid.email} isInvalid={!isValid.email && isDirty.email} onChange={handleChange} type="emal" name="email" placeholder=" ..."/>
                    <Form.Control.Feedback type="invalid">
                            
                    </Form.Control.Feedback>
                </Form.Group>
                <Button type="submit" disabled={!isValid.userName || !isValid.password || !isValid.email} variant="primary" type="submit"></Button>
            </Form>
        </>
    );
}

export default UserForm;