// src/pages/UserUpdateForm.js

import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { Button, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

//     
let savedImageSrc

function UserUpdateForm() {
    
    const [userInfo, setUserInfo] = useState({})
    //   src    state   
    const [imageSrc, setImageSrc] = useState(null)

    useEffect(()=>{
        axios.get("/user")
        .then(res=>{
            setUserInfo(res.data)
            //    
            if(res.data.profile){
                setImageSrc(`/upload/images/${res.data.profile}`)
                savedImageSrc=`/upload/images/${res.data.profile}`
            }else{// 
                // person svg   data url   imageSrc   
                // svg  2     
                const svgString=new XMLSerializer().serializeToString(personSvg.current)
                // 2  btoa (binary to ascii)   ascii  
                const encodedData = btoa(svgString)
                //  ascii   dataUrl   
                const dataUrl = "data:image/svg+xml;base64," + encodedData;
                setImageSrc(dataUrl)
                console.log(dataUrl)
                savedImageSrc=dataUrl
            }
        })
        .catch(error=>{
            console.log(error)
        })
    },[])

    //  input     
    const imageInput = useRef()
    // drop zone div     
    const dropZone = useRef()
    const personSvg = useRef()

    const dropZoneStyle={
        minHeight:"300px",
        border:"3px solid #cecece",
        borderRadius:"10px",
        display:"flex",
        justifyContent:"center",
        alignItems:"center",
        cursor:"pointer"
    }
    const profileStyle={
        width: "200px",
        height: "200px",
        border: "1px solid #cecece",
        borderRadius: "50%"
    }
    const profileStyle2={
        width: "200px",
        height: "200px",
        border: "1px solid #cecece",
        borderRadius: "50%",
        display: "none"
    }
    // input type="file"  change      
    const handleChange = (e)=>{
        //  
        const file=e.target.files[0]
        //     
        const reader=new FileReader()
        // DataURL   
        reader.readAsDataURL(file)
        // (  )    
        reader.onload=(event)=>{
            //    
            const data=event.target.result
            setImageSrc(data)
        }
    }
    // drop zone div  drop      
    const handleDrop = (e)=>{
        e.preventDefault()
        //drop     
		const file=e.dataTransfer.files[0];	
        const reg=/image/;
        if(!reg.test(file.type)){ // type     
            console.log("  ")
            return;
        }

        //     
        const reader=new FileReader()
        // DataURL   
        reader.readAsDataURL(file)
        // (  )    
        reader.onload=(event)=>{
            //    
            const data=event.target.result
            setImageSrc(data)
        }

        // input  drop     
        imageInput.current.files=e.dataTransfer.files;
    }

    const navigate = useNavigate()
    // reset      
    const handleReset = ()=>{
        setImageSrc(savedImageSrc)
    }
    // submit     
    const handleSubmit = (e)=>{
        e.preventDefault()
        //  form     FormData  
        const formData = new FormData(e.target)

        axios.patch("/user", formData, {
            headers:{"Content-Type":"multipart/form-data"}
        })
        .then(res=>{
            console.log(res.data)
            alert(".")
            navigate("/user/detail")
        })
        .catch(error=>{
            console.log(error)
        })
    }

    return (
        <>
            <svg ref={personSvg} style={profileStyle2}  xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" viewBox="0 0 16 16">
                <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0z"/>
                <path fillRule="evenodd" d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1z"/>
            </svg>
            <h1>   </h1>
            <Form onReset={handleReset} onSubmit={handleSubmit}>
                <Form.Group className="mb-3">
                    <Form.Label></Form.Label>
                    <Form.Control name="userName" defaultValue={userInfo.userName} readOnly/>
                </Form.Group>
                <Form.Group className="mb-3" controlId="email">
                    <Form.Label></Form.Label>
                    <Form.Control name="email" defaultValue={userInfo.email}/>
                </Form.Group>
                <Form.Group>
                    <Form.Label>  ( click or drag-drop to Edit ) </Form.Label>
                    <Form.Control onChange={handleChange} ref={imageInput} style={{display:"none"}} type="file" name="image" accept="image/*"/>
                </Form.Group>
                <div className="mb-3"> 
                    <a href="about:blank" onClick={(e)=>{
                        e.preventDefault()
                        // input  type="file"    
                        imageInput.current.click()
                    }}>
                        <div style={dropZoneStyle} ref={dropZone} onDragOver={(e)=>e.preventDefault()} onDrop={handleDrop}>
                            <img style={profileStyle} src={imageSrc} alt=" "/>
                        </div>
                    </a>
                </div>
                <Button type="submit" variant="success" size="sm"></Button>
                <Button className="ms-1" type="reset" variant="danger" size="sm">Reset</Button>
            </Form>
        </>
    );
}

export default UserUpdateForm;